@keyframes up {
  from{transform:rotate(0deg); }
  50%{transform:rotate(180deg);}
  100%{transform:rotate(360deg);}
}

.background {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: block;
  background-color: #111111;
  opacity: .6;
}

.image {
  width:100px;
  height:100px;
  border-radius:100%;
  position:absolute;
  border: 1px solid #702963;
  animation: up 1s;
  animation-iteration-count: infinite;
  transition:2s;
  border-bottom:none;
  border-right:none;
  animation-timing-function:linear;
  margin-left: -70px;
	margin-top: -70px;
	left:50%;
	top:50%;
}

.layer1 {
  width:90px;
  height:90px;
  left:50.35%;
  top:50.7%;
  animation-delay:.2s;
}

.layer2 {
  width:80px;
  height:80px;
  left:50.70%;
  top:51.4%;
  animation-delay:.4s;
}

.layer3 {
  width:70px;
  height:70px;
  left:51.05%;
  top:52.1%;
  animation-delay:.6s;
}

.layer4 {
  width:60px;
  height:60px;
  left:51.40%;
  top:52.8%;
  animation-delay:.8s;
}